.upload-row {
  width: 100%;
  .ant-col-2 {
    padding-left: 17px;
  }

  .ant-col-22 {
    display: flex;
    flex-wrap: wrap;

    .upload-list {
      margin-right: 20px;
      .text-box {
        display: flex;
        align-items: center;
      }
    }
     #upload-box {

       // 隐藏按钮组
       .ant-popover-buttons {
         display: none;
       }
       .ant-popconfirm {
         z-index: 100000;
       }
       .ant-popover-inner-content {
         padding: 10px;
         .ant-popover-message-title {
           padding-left: 0;
         }
         .ant-popover-message {
           padding: 0;
         }
       }
     }
  }
}
@primary-color: #1890ff;